/* 768-1023 рх */
@font-face {
  font-style: normal;
  font-weight: 100;
  font-family: "Roboto";
  font-display: swap;
  src: url("../fonts/Roboto-Thin.woff2") format("woff2"), url("../fonts/Roboto-Thin.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 300;
  font-family: "Roboto";
  font-display: swap;
  src: url("../fonts/Roboto-Light.woff2") format("woff2"), url("../fonts/Roboto-Light.woff") format("woff");
}
html {
  box-sizing: border-box;
  height: 100%;
  font-weight: 300;
  font-family: "Roboto", "Arial", sans-serif;
  color: #efe8d8;
  background-color: #27282a;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

a {
  color: inherit;
}

img {
  max-width: 100%;
  height: auto;
}

body {
  display: flex;
  flex-direction: column;
  width: 100%;
  min-width: 320px;
  min-height: 100vh;
  margin: 0;
  padding: 0;
  font-style: normal;
  font-size: 16px;
  line-height: 1.2;
}

.body--locked {
  overflow: hidden;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  width: 100%;
  max-width: 1300px;
  margin: 0 auto;
  padding: 0 50px;
}

@media (max-width: 1024px) {
  .container {
    padding: 0 30px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 20px;
  }
}
.header {
  position: relative;
  padding-bottom: 140px;
  background-image: url("../img/header-bg.png");
  background-repeat: no-repeat;
  background-position: top center;
}

.header__slipping-bg {
  position: absolute;
  left: 0;
  bottom: -69%;
  width: 100%;
}

.header__container {
  position: relative;
  display: flex;
  flex-direction: column;
  height: 100%;
  padding-top: 236px;
  padding-bottom: 30px;
}

.header__open-menu {
  position: fixed;
  top: 30px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 50;
}
.header__open-menu button {
  position: absolute;
  right: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  padding: 0;
  border: none;
  background-color: transparent;
  outline: none;
}
.header__open-menu button:not(:hover) {
  transition: 0.3s;
}
.header__open-menu button:hover {
  outline: none;
  cursor: pointer;
  transition: 0.3s;
}

.header__logo {
  max-width: 440px;
  margin-left: 266px;
  margin-bottom: 50px;
}
.header__logo img {
  margin-bottom: 50px;
}

.header__title {
  margin: 0;
  font-size: 40px;
  line-height: 47px;
  font-weight: 200;
}

.header__nav {
  position: fixed;
  width: 100%;
  height: 100vh;
  left: 0;
  top: 0;
  z-index: 100;
  padding-bottom: 50px;
  background-color: rgba(39, 40, 42, 0.9);
  opacity: 0;
  visibility: hidden;
  text-transform: lowercase;
  transition: 0.3s;
  overflow: auto;
}
.header__nav.active {
  opacity: 1;
  visibility: visible;
}

.header__nav-container {
  position: relative;
  padding-top: 97px;
}

.header__close-menu {
  position: absolute;
  right: 50px;
  top: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
  border: none;
  background-color: transparent;
  outline: none;
}
.header__close-menu:not(:hover) {
  transition: 0.3s;
}
.header__close-menu:hover {
  outline: none;
  cursor: pointer;
  transition: 0.3s;
}

.header__nav-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 50px;
}

.header__nav-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 40px;
}
.header__nav-item:last-child {
  margin-bottom: 0;
}
.header__nav-item a {
  font-size: 40px;
  line-height: 47px;
  font-weight: 200;
  text-decoration: none;
  text-align: center;
}
.header__nav-item a:not(:hover) {
  transition: 0.3s;
}
.header__nav-item a:hover {
  outline: none;
  cursor: pointer;
  transition: 0.3s;
  color: #c4a478;
}
.header__nav-item.active > a {
  margin-bottom: 30px;
  color: #c4a478;
  text-decoration: underline;
  text-decoration-skip-ink: none;
  text-decoration-thickness: from-font;
  text-underline-offset: 3px;
  transition: 0.3s;
}
.header__nav-item.active .header__subnav-list {
  display: flex;
}

.header__subnav-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: none;
  flex-direction: column;
  align-items: center;
}
.header__subnav-list li {
  margin-bottom: 20px;
}
.header__subnav-list li:last-child {
  margin-bottom: 0;
}
.header__subnav-list a {
  font-size: 20px;
  line-height: 26px;
  font-weight: 300;
}

.header__nav-reserv {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0;
  font-size: 20px;
  line-height: 26px;
  font-weight: 300;
  text-align: center;
}
.header__nav-reserv a {
  margin-top: 5px;
  text-decoration: none;
}
.header__nav-reserv a:not(:hover) {
  transition: 0.3s;
}
.header__nav-reserv a:hover {
  outline: none;
  cursor: pointer;
  transition: 0.3s;
  color: #c4a478;
}

@media (max-width: 1024px) {
  .header__logo {
    margin-left: 200px;
  }

  .header__open-menu button {
    right: 30px;
  }
}
@media (max-width: 767px) {
  .header {
    background-image: url("../img/header-bg-mobile.png");
    background-repeat: no-repeat;
    background-position: top center;
    background-size: cover;
  }

  .header__container {
    padding-top: 195px;
  }

  .header__open-menu button {
    right: 20px;
  }

  .header__close-menu {
    right: 20px;
  }

  .header__title {
    font-size: 20px;
    line-height: 26px;
  }

  .header__logo {
    max-width: 165px;
    margin: 0 auto 200px;
  }
  .header__logo img {
    width: 150px;
    margin-bottom: 30px;
  }

  .header__nav-item {
    margin-bottom: 25px;
  }
  .header__nav-item a {
    font-size: 30px;
    line-height: 37px;
  }
  .header__nav-item.active > a {
    margin-bottom: 15px;
  }

  .header__subnav-list li {
    margin-bottom: 10px;
  }
  .header__subnav-list a {
    font-size: 18px;
    line-height: 24px;
  }

  .header__nav-reserv {
    font-size: 18px;
    line-height: 24px;
  }
}
.link-down {
  position: relative;
  z-index: 2;
  display: flex;
  align-items: center;
  margin-top: auto;
  color: #c4a478;
  text-decoration: none;
}
.link-down svg {
  margin-right: 26px;
}
.link-down span {
  max-width: 100px;
}

@media (max-width: 767px) {
  .link-down {
    margin-left: auto;
    margin-right: auto;
    justify-content: center;
  }
}
.section-header {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-bottom: 100px;
  z-index: 10;
  position: relative;
}
.section-header .section-title {
  margin-right: 13px;
}

.section-header__links {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  margin-top: 5px;
}
.section-header__links li {
  padding: 0 10px;
  border-right: 1px solid #efe8d8;
}
.section-header__links li a {
  font-size: 16px;
  line-height: 19px;
  text-decoration: none;
  text-transform: lowercase;
}
.section-header__links li a:not(:hover) {
  transition: 0.3s;
}
.section-header__links li a:hover {
  outline: none;
  cursor: pointer;
  transition: 0.3s;
  color: #c4a478;
}
.section-header__links li a.active {
  color: #c4a478;
  text-decoration: underline;
  text-decoration-skip-ink: none;
  text-decoration-thickness: from-font;
  text-underline-offset: 2px;
  transition: 0.3s;
}
.section-header__links li:last-child {
  border-right: 0;
}

@media (max-width: 1024px) {
  .section-header {
    margin-bottom: 66px;
  }
}
@media (max-width: 767px) {
  .section-header {
    flex-direction: column;
    align-items: flex-start;
  }
  .section-header .section-title {
    margin-right: 0;
    margin-bottom: 23px;
  }

  .section-header__links {
    margin: 0;
  }
  .section-header__links li:first-child {
    padding-left: 0;
  }
}
.section-title {
  margin: 0;
  font-size: 60px;
  line-height: 70px;
  font-weight: 200;
  text-transform: lowercase;
}

@keyframes smooth {
  from {
    opacity: 1;
    visibility: visible;
  }
  to {
    opacity: 0;
    visibility: hidden;
  }
}
.menu {
  position: relative;
  margin-bottom: 50px;
}
.menu .link-down {
  display: none;
}
.menu .section-title .menu__back-link {
  display: none;
}

.menu__links {
  padding-bottom: 65px;
  background-image: url("../img/menu-links-bg.png");
  background-repeat: no-repeat;
  background-position: center bottom 45px;
}
.menu__links::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: #27282a;
  z-index: 1;
  animation-duration: 1s;
  animation-delay: 1s;
  animation-fill-mode: forwards;
  opacity: 1;
}
.menu__links.hidden-bg::after {
  animation-name: smooth;
}
.menu__links.hidden {
  opacity: 0;
  visibility: hidden;
  position: absolute;
}
.menu__links a {
  display: flex;
  justify-content: center;
  width: 100%;
  z-index: 1;
}
.menu__links a:not(:hover) {
  transition: 0.3s;
}
.menu__links a:hover {
  outline: none;
  cursor: pointer;
  transition: 0.3s;
}
.menu__links a.blur {
  filter: blur(5px);
}

.menu__links__tip {
  display: none;
}

.menu__links-wrapper {
  position: relative;
  display: flex;
  justify-content: center;
}

.menu__dark-img {
  position: absolute;
  z-index: 2;
  animation-duration: 1s;
  animation-delay: 1s;
  animation-fill-mode: forwards;
  opacity: 1;
}

.menu__link--cigar {
  position: absolute;
  bottom: -3px;
  left: 50%;
  transform: translateX(-50%);
}
.menu__link--cigar:not(:hover) {
  transition: 0.3s;
}
.menu__link--cigar::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 57px;
  background-image: url("../img/cigar-gradient.png");
  background-repeat: no-repeat;
  background-position: center;
}

.menu__part {
  padding-bottom: 80px;
  background-repeat: no-repeat;
  opacity: 0;
  visibility: hidden;
  position: absolute;
}
.menu__part.visible {
  opacity: 1;
  visibility: visible;
  position: static;
}
.menu__part .container {
  display: flex;
  flex-wrap: wrap;
}
.menu__part--cigar {
  background-image: url("../img/menu-part-cigar-bg.png");
  background-position: left bottom;
}
@media (max-width: 1600px) {
  .menu__part--cigar {
    background-position: left -100px bottom;
  }
}
@media (max-width: 1400px) {
  .menu__part--cigar {
    background-size: 75%;
    background-position: left -70px bottom;
  }
}
.menu__part--wine {
  background-image: url("../img/menu-part-wine-bg.png");
  background-position: left bottom;
}
@media (max-width: 1850px) {
  .menu__part--wine {
    background-position: left -100px bottom;
  }
}
@media (max-width: 1650px) {
  .menu__part--wine {
    background-position: left -150px bottom;
  }
}
@media (max-width: 1550px) {
  .menu__part--wine {
    background-position: left -200px bottom;
  }
}
@media (max-width: 1450px) {
  .menu__part--wine {
    background-position: left -250px bottom;
  }
}
@media (max-width: 1350px) {
  .menu__part--wine {
    background-position: left -300px bottom;
  }
}
@media (max-width: 1250px) {
  .menu__part--wine {
    background-size: 90%;
    background-position: left -225px bottom 100px;
  }
}
@media (max-width: 1100px) {
  .menu__part--wine {
    background-size: 95%;
    background-position: left -210px bottom 100px;
  }
}
.menu__part--wine .menu__part-left p {
  max-width: 262px;
  margin-top: auto;
  margin-left: auto;
  margin-bottom: 82px;
  color: #27282a;
}
@media (max-width: 1250px) {
  .menu__part--wine .menu__part-left p {
    margin-bottom: 145px;
  }
}
@media (max-width: 1100px) {
  .menu__part--wine .menu__part-left p {
    max-width: 215px;
    margin-bottom: 125px;
  }
}

.menu__part-left {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 36.5%;
  margin-right: 4%;
}
.menu__part-left p {
  max-width: 332px;
  margin: 0;
  margin-left: 46px;
  font-size: 20px;
  line-height: 26px;
  color: #c4a478;
}

.menu__back-link {
  display: flex;
  justify-content: center;
  margin-bottom: 50px;
}
.menu__back-link:not(:hover) {
  transition: 0.3s;
}
.menu__back-link:hover {
  outline: none;
  cursor: pointer;
  transition: 0.3s;
}

.menu__part-list {
  width: 59.5%;
  height: 564px;
  overflow: auto;
  /* W3C standard
   сейчас только для Firefox */
  scrollbar-width: thin;
  scrollbar-color: #c4a478 #232325;
  /* для Chrome/Edge/Safari */
}
.menu__part-list::-webkit-scrollbar {
  width: 13px;
}
.menu__part-list::-webkit-scrollbar-track {
  background: #232325;
  border-radius: 20px;
}
.menu__part-list::-webkit-scrollbar-thumb {
  background-color: #c4a478;
  border-radius: 20px;
  border: 3px solid #232325;
}
.menu__part-list h3 {
  margin: 0;
  margin-bottom: 26px;
  font-size: 20px;
  line-height: 26px;
  font-weight: 300;
  color: #c4a478;
}
.menu__part-list table {
  width: 100%;
  height: 100%;
  border-collapse: collapse;
  border-spacing: 0;
}
.menu__part-list table td {
  padding: 0;
  padding-bottom: 10px;
  vertical-align: top;
}
.menu__part-list table td:first-child {
  padding-right: 30px;
}
.menu__part-list table td:last-child {
  padding-right: 35px;
}

@media (max-width: 1024px) {
  .menu .section-title {
    display: flex;
    align-items: center;
  }
  .menu .section-title .menu__back-link {
    display: flex;
    margin-bottom: 0;
    margin-right: 20px;
  }

  .menu__part {
    padding-bottom: 405px;
  }
  .menu__part .container {
    flex-direction: column;
  }
  .menu__part--cigar {
