.filter {
  display: none;
  flex-direction: column;
  margin-bottom: 38px;
  border-top: 1px solid rgba(239, 232, 216, 0.1);
  border-bottom: 1px solid rgba(239, 232, 216, 0.1);
}
.filter.active .filter__header {
  border-bottom: 1px solid rgba(239, 232, 216, 0.1);
}
.filter.active .filter__header::after {
  transform: translateY(-50%) rotate(-180deg);
}
.filter.active .filter__parameters {
  display: flex;
}

.filter__header {
  padding: 0;
  border: none;
  background-color: transparent;
  outline: none;
  font-size: 16px;
  line-height: 19px;
  position: relative;
  width: 100%;
  height: 100%;
  padding: 10px 50px 10px 0;
  text-align: left;
  color: inherit;
}
.filter__header:not(:hover) {
  transition: 0.3s;
}
.filter__header::after {
  content: "";
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
  width: 25px;
  height: 25px;
  background-image: url("../img/icon-filter-arrow.svg");
  background-repeat: no-repeat;
  background-position: center;
  transition: 0.3s;
}

.filter__parameters {
  display: none;
  flex-direction: column;
}
.filter__parameters label span {
  font-size: 16px;
  line-height: 19px;
  display: flex;
  width: 100%;
  padding: 10px 0;
  white-space: nowrap;
}
.filter__parameters label input:checked + span {
  color: #c4a478;
}

@media (max-width: 767px) {
  .filter {
    display: flex;
  }
}
.interior {
  margin-bottom: 150px;
}

.interior__content {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.interior__left {
  width: 27%;
  margin-right: 2%;
}
.interior__left div {
  display: none;
}
.interior__left div h2 {
  margin: 0 0 100px 31px;
  font-size: 40px;
  line-height: 47px;
  font-weight: 200;
  color: #c4a478;
}
.interior__left div p {
  margin: 0;
  font-size: 20px;
  line-height: 26px;
}

.interior__right {
  display: flex;
  flex-wrap: wrap;
  width: 71%;
}

.interior__slider-wrapper {
  position: relative;
  width: 58.5%;
}

.interior__slider-container {
  overflow: hidden;
}

.interior__slider {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.interior__slide {
  display: flex;
  flex-shrink: 0;
}
.interior__slide img {
  width: 100%;
  height: 100%;
  max-height: 600px;
  object-fit: cover;
}

.interior__next-slide-preview {
  position: relative;
  width: 20.75%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: left top;
  filter: blur(2.5px);
  transition: 0.3s;
}
.interior__next-slide-preview::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(92deg, #27282A -18.19%, rgba(39, 40, 42, 0) 115.23%);
}

.interior__after-next-slide-preview {
  position: relative;
  width: 20.75%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: left top;
  filter: blur(5px);
  transition: 0.3s;
}
.interior__after-next-slide-preview::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(89deg, rgba(39, 40, 42, 0) -275.71%, #27282A 130.95%);
}

@media (max-width: 1024px) {
  .interior__content {
    flex-direction: column;
  }

  .interior__left {
    order: 1;
    width: 100%;
    margin-right: 0;
  }
  .interior__left div h2 {
    margin: 0 0 30px 0;
  }

  .interior__right {
    width: 100%;
    margin-bottom: 30px;
  }

  .interior__slider-wrapper .slider-btn {
    display: none;
  }
}
@media (max-width: 767px) {
  .interior {
    margin-bottom: 100px;
  }

  .interior__left div p {
    font-size: 16px;
    line-height: 19px;
  }

  .interior__slide img {
    max-height: 316px;
  }

  .interior__slider-wrapper {
    width: 88%;
  }

  .interior__next-slide-preview {
    width: 6%;
  }

  .interior__after-next-slide-preview {
    width: 6%;
  }
}
.slider-btn {
  position: absolute;
  z-index: 1;
  bottom: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
  padding: 5px 0;
}
.slider-btn:not(:hover) {
  transition: 0.3s;
}
.slider-btn.swiper-button-disabled {
  opacity: 0.3;
  cursor: auto;
}
.slider-btn:hover {
  outline: none;
  cursor: pointer;
  transition: 0.3s;
}
.slider-btn--prev {
  left: -51px;
}
.slider-btn--next {
  right: -51px;
}
.slider-btn--next svg {
  transform: rotate(180deg);
}