.link-down {
  position: relative;
  z-index: 2;
  display: flex;
  align-items: center;
  margin-top: auto;
  color: $golden;
  text-decoration: none;

  svg {
    margin-right: 26px;
  }

  span {
    max-width: 100px;
  }
}

@media (max-width: $tablet-width) {
  .link-down {
    margin-left: auto;
    margin-right: auto;
    justify-content: center;
  }
}
