.interior {
  margin-bottom: 150px;
}

.interior__content {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.interior__left {
  width: 27%;
  margin-right: 2%;

  div {
    display: none;

    h2 {
      margin: 0 0 100px 31px;
      @include font(40, 47);
      font-weight: 200;
      color: $golden;
    }

    p {
      margin: 0;
      @include font(20, 26);
    }
  }
}

.interior__right {
  display: flex;
  flex-wrap: wrap;
  width: 71%;
}

.interior__slider-wrapper {
  position: relative;
  width: 58.5%;
}

.interior__slider-container {
  overflow: hidden;
}

.interior__slider {
  @include list-reset;
  display: flex;
}

.interior__slide {
  display: flex;
  flex-shrink: 0;

  img {
    width: 100%;
    height: 100%;
    max-height: 600px;
    object-fit: cover;
  }
}


.interior__next-slide-preview {
  position: relative;
  width: 20.75%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: left top;
  filter: blur(2.5px);
  transition: $style-change-duration;

  &::after {
    @include pseudo-elem;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(92deg, #27282A -18.19%, rgba(39, 40, 42, 0.00) 115.23%);
  }
}

.interior__after-next-slide-preview {
  position: relative;
  width: 20.75%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: left top;
  filter: blur(5px);
  transition: $style-change-duration;

  &::after {
    @include pseudo-elem;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(89deg, rgba(39, 40, 42, 0.00) -275.71%, #27282A 130.95%);
  }
}

@media (max-width: $desktop-width) {
  .interior__content {
    flex-direction: column;
  }

  .interior__left {
    order: 1;
    width: 100%;
    margin-right: 0;

    div {
      h2 {
        margin: 0 0 30px 0;
      }
    }
  }

  .interior__right {
    width: 100%;
    margin-bottom: 30px;
  }

  .interior__slider-wrapper {
    .slider-btn {
      display: none;
    }
  }
}

@media (max-width: $tablet-width) {
  .interior {
    margin-bottom: 100px;
  }

  .interior__left {
    div {
      p {
        @include font(16, 19);
      }
    }
  }

  .interior__slide {
    img {
      max-height: 316px;
    }
  }

  .interior__slider-wrapper {
    width: 88%;
  }

  .interior__next-slide-preview {
    width: 6%;
  }

  .interior__after-next-slide-preview {
    width: 6%;
  }
}
